import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import Logo from '/src/components/Logo/Logo.js'
import MenuList from './menu/menu-list.js'
import MenuSocial from './social/social-list'
import ThemeToggle from './theme-toggle/theme-toggle'
import WebCrypto from '/src/components/WebCrypto/WebCrypto.js'
import './header.scss'

const Header = ({ siteTitle, menu, social }) => {
  return (
    <header className="header">
      <div className="header__content">
        <Link to="/" className="logo-wrapper">
          <Logo />
        </Link>

        <input className="menu-btn" type="checkbox" id="menu-btn" />
        <label className="menu-icon" htmlFor="menu-btn"><span className="navicon"></span></label>
        
        
        <ThemeToggle items={social} />
        <MenuSocial items={social} />
        <WebCrypto />
        <MenuList items={menu} />
      </div>
    </header>
  )
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
