import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

import "./menu-list.scss";

const MenuList = ({ items }) => {
    return (
        <ul className="menu">
            {items.map(({ label, path, partially },index) => (
                <li key={index}>
                    <Link to={path} partiallyActive={partially} activeClassName="link-active">
                    {label}
                    </Link>
                </li>))
            }
        </ul>
    )
}

MenuList.propTypes = {
    items: PropTypes.array,
}

export default MenuList