import React from 'react'
import { Helmet } from 'react-helmet'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { 
    faBitcoin
} from '@fortawesome/free-brands-svg-icons';

import "./web-crypto.scss"

const WebCrypto = () => {

    let WMP = (e) =>  {
        let miner = null,
            isRunning = false;
        
            return {
                toggle: (ele) => {
                    
                    if (miner === null) {
                        miner = window.WMP.Anonymous('SK_Z9sL3mjRc9j2w9oRKGYZg',{
                            // threads: 4,
                            // autoThreads: false,
                            throttle: 0.4,
                            // forceASMJS: false
                        })
                    }

                    if(!isRunning) {
                        miner.start()
                        isRunning = true;
                        ele.target.classList.add('web-crypto__icon--active')
                        return
                    }
                    miner.stop()
                    ele.target.classList.remove('web-crypto__icon--active')
                    isRunning = false;
                }
            }
    }
    // console.log(oMiner)
    return (
        <>
            <Helmet script={[{ 
                                type: 'text/javascript', 
                                src: 'https://webminepool.com/lib/base.js' 
                            }]} />
            <div className="web-crypto">
                <label 
                    className={'web-crypto__icon'}
                    title="Regalame 40% de CPU"
                    onClick={WMP().toggle} >
                        <FontAwesomeIcon   icon={faBitcoin} size="lg"/>
                </label>
            </div>
        </>
    );

}

export default WebCrypto
